@import './_reset.scss';
@import './_variables.scss';
@import './_global-class.scss';
@import './_header.scss';
@import './_logo.scss';





.box__content {}
.container {}
.box__content__row {
  display: flex;
}

.box__content__row__col-left {
  position: relative;
  width: 60%;
  overflow: hidden;
}
.box__content__row__col-left__box {
  position: absolute;
  top: 40%;
  height: 300px;
  width: 460px;
  overflow: hidden;
  box-shadow: 0 8px 28px -6px rgba(0,0,0,0.75);
}
.box__content__row__col-left__box:hover .box__content__row__col-left__box__title {
  transform: scale(0.35) translateX(-300px);
}
.box__content__row__col-left__box:hover .box__content__row__col-left__box__img {
  transition: 1s all;
  transform: scale(1.2, 1.2);
}

.box__content__row__col-left__box__img {
  width: 100%;
  height: 100%;
  display: block;
  transition: 0.5s all;

}

.box__content__row__col-left__box__title {
  position: absolute;
  top: 70%;
  width: 100%;
  font-weight: bold;
  font-family: $headerFont;
  font-size: 25px;
  color: black;
  transition: 2s all;


  &:after {
    content: '';
    position: absolute;
    top: -20px;
    bottom: -20px;
    left: 0;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.5);
  }

}


.box__content__row__col-left__description,
.fa-angle-double-down {
  text-align: center;
  position: absolute;
  top: 25%;
  left: 50%;

  font-size: 30px;
}
.box__content__row__col-left__description {
  transform: translateX(-50%);
}
.fa-angle-double-down {
  top: 30%;
  animation-name: movingDown;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@keyframes movingDown {
  50% {transform: translateY(12px)}
}



.box__content__row__col-right {
  display: flex;
  flex-direction: column;
}

.box__content__row__col-right__header {
  display: flex;
  justify-content: center;
  padding-left: 1rem;
  margin-bottom: 1.5rem;
}
.box__content__row__col-right__header__logo {
  width: 120px;
  height: 120px;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 60px;
}

.box__content__row__col-right__header__logo__img {
  height: 100%;
  width: 100%;
}
.logo-overlay {
  transition: 0.5s all;
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30px;
    height: 2px;
    background-color: green;
  }
  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(90deg);
    width: 30px;
    height: 2px;
    background-color: green;
  }
}
.box__content__row__col-right__header__logo:hover .logo-overlay {
  opacity: 1;
}
.box__content__row__col-right__header__text {
  text-align: left;
  margin-bottom: 2rem;
}
.box__content__row__col-right__header__text__title {
  font-size: 3.2rem;
  font-weight: bold;
  padding-top: 8px;
}
.box__content__row__col-right__header__text__para {
  margin-top: 15px;
}
.box__content__row__col-right__quote {
  margin-bottom: 2rem;
}
.box__content__row__col-right__quote__title {
  font-size: 1.9rem;
  font-weight: bold;
  margin-bottom: 1rem;
}
.box__content__row__col-right__quote__para {
  margin-left: 8px;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: -8px;
    top: 50%;
    width: 6px;
    height: 1px;
    background-color: #333;
  }
}
.box__content__row__col-right__quote__para > span {
  text-decoration: underline;
}

.box__content__row__col-right__advantages {
  margin-left: 9rem;
  list-style: none;
}
.box__content__row__col-right__advantages__item {
  font-weight: bold;
  margin-bottom: 2rem;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 10px;
    left: -30px;
    width: 8px;
    height: 3px;
    background-color: green;
    transform: rotate(37deg);
    border-radius: 50%;
  }

  &:before {
    content: '';
    position: absolute;
    top: 6px;
    left: -27px;
    width: 15px;
    height: 3px;
    background-color: green;
    transform: rotate(120deg);
    border-radius: 50%;
  }
}


.box__content__row__col-right__advisory {
  padding-left: 2rem;
}
.box__content__row__col-right__advisory__box {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 2rem;
}
.box__content__row__col-right__advisory__box__icon-git,
.box__content__row__col-right__advisory__box__icon-ring {
  font-size: 4rem;
  color: #000;

}
.box__content__row__col-right__advisory__box__icon-git .fa-github {
  animation-name: shaking;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-delay: 4s;
}

@keyframes shaking {
  10% {transform: translateY(-8px); transform-origin: center}
  15% {transform: rotate(15deg) translateY(-8px); transform-origin: bottom}
  25% {transform: rotate(-15deg) translateY(-8px); transform-origin: bottom}
  35% {transform: rotate(15deg) translateY(-8px); transform-origin: bottom}
}
.box__content__row__col-right__advisory__box__icon-ring {
  transition: 1s all;
  position: relative;
  background-color: transparent;
}
.box__content__row__col-right__advisory__box__icon-ring:hover:after {
    content: '';
    position: absolute;
    top: -5%;
    left: -5%;
    bottom: -5%;
    right: -5%;
    border-radius: 50%;
    background-color: rgba(#E8570C, 0.2);

}
.box__content__row__col-right__advisory__link {
  text-decoration: none;
  color: $colorFont;
  display: block;
  text-align: center;
  background-color: #d9e61e;
  padding: 5px 15px;
  position: relative;

  &:hover {
    color: #fff;
    
  }
}

